import React from "react";
import {  useEffect } from "react";
import {useDispatch, useSelector} from "react-redux"
import { getCountries } from "../actions";
import { Link } from "react-router-dom";
import Card from "./Card"; 

export default function Home(){
    const dispatch = useDispatch()
    const allCountries = useSelector((state) => state.countries)
    
    useEffect(()=>{
        dispatch(getCountries())
    },[dispatch])

    function handleClick(e){
        e.preventDefault()
        dispatch(getCountries())

    }


    return (
        <div>
            <Link to = "/countries"> Ver paises</Link>
            <h1>Countries #1</h1>
            <button onClick={e=> {handleClick(e)}}>
                refrescar paises
            </button>
            <div>
                <select>
                    <option value="asc">Ascendente</option>
                    <option value="desc" >Descendente</option>
                    <option value="poblation" >Poblacion</option>
                </select>
                <select>
                    <option value="region" >Continente</option>
                    <option value="activity" >Avtividades Turisticas</option>
                </select>

                {allCountries?.map( (el) =>{
                    return(
                        <fragment>
                            <Link to={"/home/" + el.id}>
                            <Card id={el.id} name={el.name} continent={el.continent} imgFlag={el.imgFlag}/>
                            </Link>
                        </fragment>
                    )
                } 
                    
                    )
                }
            </div>
        </div>
    )
}